import React, { PureComponent, Component } from "react";
import { POPUP_IMAGE_RENEW_REQUEST } from '../../../../actions/themeActions'
import { getImageSize } from 'react-image-size';
import Hotspot from "../../../Themes/Elements/Hotspot/Hotspot";
import { GET_MEDIA_NAME } from "../../../../utils";

export default class PopUpImage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mediaLoaded: false,
      largerHeight: 0,
      imgSize: {
        small: props.data.postFileNew,
        large: props.data.filename,
        orignal: props.data.file
      }
    };
  }

  componentWillReceiveProps(nextProps) {
    const { data } = nextProps
    if (this.props.data !== data) {
      this.setState({
        imgSize: {
          small: data.postFileNew,
          large: data.filename,
          orignal: data.file
        }
      })
    }
  }
  render() {
    const { data, wall, opacity, mediaSize } = this.props
    const { mediaLoaded, imgSize } = this.state
    const isHotspot = data.hotspot && data.ugc_products.length > 0 ? true : false
    const imgStyle = {
      opacity: opacity, filter: mediaLoaded ? 'blur(2px)' : null,
      //aspectRatio: `${mediaSize.width}/${mediaSize.height}` 
    }
    return (
      <div className={`tb_post_modal_img_holder`}>
        {isHotspot ? <Hotspot product={data.ugc_products} /> : null}
        <img
          className="tb_post_modal_img_"
          decoding="async"
          ref={this.mediaRef}
          src={imgSize.small}
          srcSet={`${imgSize.large}, 991w ${imgSize.small}, 480w`}
          sizes={mediaSize && mediaSize.width}
          alt={GET_MEDIA_NAME(imgSize.small)}
          data-link={data.link}
          data-network={data.network.id}
          data-wall-id={wall.Wall.id}
          data-owner-id={wall.Wall.owner}
          data-item-id={data.id}
          data-feed-id={data.feedId ? data.feedId : ""}
          data-filter-id={data.filterId}
          data-load="0"
          onLoad={this.handleImageLoad}
          onError={!data.stories ? (e) => {
            //POPUP_IMAGE_RENEW_REQUEST(e) 
          } : null}
          key={imgSize.small}
          loading="lazy"
          draggable="false"
          width={mediaSize && mediaSize.width}
          height={mediaSize && mediaSize.height}
          style={imgStyle} />
      </div>
    );
  }
}

