import React, { PureComponent, Suspense } from "react";
import { connect } from 'react-redux';
import { componentWillAppendToBody } from "react-append-to-body";
import { Tooltip } from 'react-tooltip';
import "./scss/Hotspot.scss";
import { generateUniqueString } from '../../../../utils'

const MyComponent = ({ children }) => {
  return children;
}

const AppendedView = componentWillAppendToBody(MyComponent);
const htmlString = (product) => {
  const discountPrice = product.product_discount > 0 ? product.product_discount : product.product_price;
  const originalPrice = product.product_discount > 0 && product.product_discount != product.product_price ?
    `<div class="tb_p_tooltip_price tb_price_disabled">${product.price_currency_symbol}${product.product_price}</div>` : '';
  const priceHtml = product.product_price > 0 || product.product_discount > 0 ?
    `${originalPrice}<div class="tb_p_tooltip_price">${product.price_currency_symbol}${discountPrice}</div>` : '';

  return `<a href="${product.product_url}" target="_blank" class="tb_tooltip_wrap">
          <div class="tb_p_tooltip_title">${product.product_title}</div>
          ${priceHtml}
        </a>`;
}

class Hotspot extends PureComponent {
  state = { activeMouse: null }

  setActiveMouse = (index) => {
    return index
  }
  render() {
    const { product, appData } = this.props;
    const { activeMouse } = this.state;
    const Hotspot = appData && Object.keys(appData).length > 0 ? appData.wall.ProductSetting.Hotspot : ''
    return (Hotspot.status ? <div className="tb_hotspot_wrap">
      {product.map((item, index) => {
        const uniqueString = generateUniqueString();
        return <div key={index} href={item.product_url} id={`tb_hotspot-${item.id}${uniqueString}`} onClick={event => { event.stopPropagation() }} onMouseEnter={event => this.setActiveMouse(index)} onMouseOut={event => this.setActiveMouse(null)} className={`tb_hotspot_dot${activeMouse == index ? ' tb_hotspot_active_' : ''}`} style={{ top: item.hotspot_cordinates.top, left: item.hotspot_cordinates.left }} data-tooltip-offset={7}>
          <div className={`tb_hotspot__ ${Hotspot.type == 0 ? 'tb_hotspot_ani__' : ''}`} onClick={event => window.open(item.UgcProduct.product_url, '_blank')}>{index + 1}</div>
          <AppendedView><Tooltip className="tb_pro_tooltip" anchorSelect={`#tb_hotspot-${item.id}${uniqueString}`} clickable html={htmlString(item.UgcProduct)} /></AppendedView>
        </div>
      })
      }
    </div >
      : null
    );
  }
}

const mapStateToProps = state => {
  return {
    appData: state.appData
  }
}

export default connect(mapStateToProps)(Hotspot);